import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import vuetify from "./plugins/vuetify";
import vueToasted from "vue-toasted";

Vue.config.productionTip = false;

Vue.use(vueToasted, {
  duration: 4000,
  position: 'bottom-right',
});

new Vue({
  router,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
